import {randomID} from '../utilities';

class Bill {
  constructor(a) {
    this.id =  randomID();
    this.order_id = a.order_id;
    this.ticket_id = a.ticket_id;
    this.items = [];
    this.total = a.total || this.items ? +this.items.reduce((acc, c) => +acc + (+c.final_price * +c.quantity), 0) : 0;
    this.created_time = Date.now();
    this.completed_time = null;
    this.is_done = false;
    this.type = a.type;
  }
}
export default Bill;



