              <th scope="col">Created Date</th>
              <th scope="col">Completed Date</th>
              <th scope="col">Status</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr role="button" v-for="(tic, index) in ticket" :key="index">
              <th scope="row">{{index + 1}}</th>
              <td>{{tic.id}}</td>
              <td>{{new Date(tic.created_time).toLocaleDateString()}}</td>
              <td>{{new Date(tic.completed_time).toLocaleDateString()}}</td>
              <td>{{tic.is_confirmed ? 'Done' : 'Pending'}}</td>
              <td>
                <button class="btn btn-primary" @click="fetchOrder(tic.order_id, index)">Detail</button>
                <button
                  class="btn btn-primary"
                  @click="confirmTicket(tic)"
                  v-if="!tic.is_confirmed"
                >Confirm</button>
                <button
                  class="btn btn-primary"
                  @click="rejectTicket(tic)"
                  v-if="tic.is_confirmed"
                >Reject</button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="card" v-if="item">
          <div class="card-header">
            <h3>Order ID: {{item.id}}</h3>
          </div>
          <div class="card-body">
            <p>Name: {{item.name}}</p>
            <p>Date: {{item.created_time}}</p>
            <p>Employee: {{item.employee_id}}</p>
            <p>Status: {{item.is_done ? 'Done' : 'Pending'}}</p>
            <p>
              <a
                class="btn btn-primary"
                data-toggle="collapse"
                :href="'#'+item.id"
                role="button"
                aria-expanded="false"
                :aria-controls="item.id"
              >Items</a>
            </p>
            <div class="collapse" :id="item.id">
              <div class="card card-body m-1" v-for="(item, index) in item.items" :key="index">
                <p>ID: {{item.id}}</p>
                <p>Name: {{item.name}}</p>
                <p>Price: {{item.price}}</p>
                <p>Discount percent: {{item.discount_percent}}%</p>
                <p>Discount value: {{item.discount_value}}</p>
                <p>Quantity: {{item.quantity}}</p>
                <p>Stock: {{item.stock}}</p>
                <p>Added Time: {{item.created_time}}</p>
                <p>Final price: {{item.final_price}}</p>
                <p>Total: {{+item.final_price * +item.quantity}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div id="import" class="container tab-pane">
        <div class="form-group">
          <input
            type="text"
            class="form-control"
            placeholder="Search"
            @keyup="search2()"
            v-model="searchID2"
          >
        </div>
        <table class="table">
          <thead class="thead-dark">
            <tr>
              <th scope="col">#</th>
              <th scope="col">ID</th>
              <th scope="col">Created Date</th>
              <th scope="col">Completed Date</th>
              <th scope="col">Status</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr role="button" v-for="(tic, index) in importTicket" :key="index">
              <th scope="row">{{index + 1}}</th>
              <td>{{tic.id}}</td>
              <td>{{new Date(tic.created_time).toLocaleDateString()}}</td>
              <td>{{new Date(tic.completed_time).toLocaleDateString()}}</td>
              <td>{{tic.is_confirmed}}</td>
              <td>
                <button
                  class="btn btn-primary"
                  @click="fetchImportOrder(tic.order_id, index)"
                >Detail</button>
                <button
                  class="btn btn-primary"
                  @click="confirmImportTicket(tic)"
                  v-if="!tic.is_confirmed"
                >Confirm</button>
                <button
                  class="btn btn-primary"
                  @click="rejectImportTicket(tic)"
                  v-if="tic.is_confirmed"
                >Reject</button>
              </td>
            </tr>
          </tbody>
        </table>
        <!-- <div class="card" v-if="order">
          <div class="card-header">
            <h3>Order ID: {{order.id}}</h3>
          </div>
          <div class="card-body">
            <p>Name: {{order.name}}</p>
            <p>Date: {{order.created_time}}</p>
            <p>Employee: {{order.employee_id}}</p>
            <p>Status: {{order.is_done ? 'Done' : 'Pending'}}</p>
            <p>
              <a
                class="btn btn-primary"
                data-toggle="collapse"
                :href="'#' + order.id"
                role="button"
                aria-expanded="false"
                :aria-controls="order.id"
              >Items</a>
            </p>
            <div class="collapse" :id="order.id">
              <div class="card card-body m-1" v-for="(item, index) in order.items" :key="index">
                <p>ID: {{item.id}}</p>
                <p>Name: {{item.name}}</p>
                <p>Price: {{item.price}}</p>
                <p>Discount percent: {{item.discount_percent}}%</p>
                <p>Discount value: {{item.discount_value}}</p>
                <p>Quantity: {{item.quantity}}</p>
                <p>Stock: {{item.stock}}</p>
                <p>Added Time: {{item.created_time}}</p>
                <p>Final price: {{item.final_price}}</p>
                <p>Total: {{+item.final_price * +item.quantity}}</p>
              </div>
            </div>
          </div>
        </div> -->
      </div>
      <div id="item-info" class="container tab-pane">
        <table class="table">
          <thead class="thead-dark">
            <tr>
              <th scope="col">#</th>
              <th scope="col">ID</th>
              <th scope="col">Name</th>
              <th scope="col">Price</th>

              <th scope="col">Stock</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr role="button" v-for="(tic, index) in items" :key="index">
              <th scope="row">{{index + 1}}</th>
              <td>{{tic.id}}</td>
              <td>{{tic.name}}</td>
              <td>{{tic.price}}</td>
              <td>{{tic.stock}}</td>
              <td>{{tic.is_confirmed}}</td>
              <td>
                <button class="btn btn-primary" @click="fetchOrder(tic.id, index)">Detail</button>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="card container">
          <h4>Update item</h4>
          <div class="form-group">
            <label class="col-md-4 control-label" for="textinput">product name</label>
            <div class="col-md-4">
              <input
                id="textinput"
                name="textinput"
                type="text"
                placeholder
                class="form-control input-md"
                v-model="item.name"
              >
            </div>
          </div>

          <!-- Text input-->
          <div class="form-group">
            <label class="col-md-4 control-label" for="textinput">price - unit</label>
            <div class="col-md-4">
              <input
                id="textinput"
                name="textinput"
                type="number"
                placeholder
                class="form-control input-md"
                v-model="item.price"
              >
            </div>
          </div>

          <!-- Text input-->
          <div class="form-group">
            <label class="col-md-4 control-label" for="textinput">stock</label>
            <div class="col-md-4">
              <input
                id="textinput"
                name="textinput"
                type="number"
                placeholder
                class="form-control input-md"
                v-model="item.stock"
              >
            </div>
          </div>

          <div class="form-group">
            <label class="col-md-4 control-label" for="singlebutton"></label>
            <div class="col-md-4">
              <button
                id="singlebutton"
                name="singlebutton"
                class="btn btn-success"
                @click="update()"
              >Update</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import storageManage from "../modules/storageManage";
import u from "../modules/util";

import db from "../configs/firebase.config";
import Bill from "../models/bill.model";
import { storage } from "firebase";
// import saleManage from "../modules/saleManage";

export default {
  name: "Storage",
  data() {
    return {
      ticket: [],
      importTicket: [],
      order: "",
      items: [],
      item: {},
      searchID: "",
      searchID2: ""
    };
  },
  methods: {
    update() {
      this.item.stock = +this.item.stock;
      this.item.price = +this.item.price;
   
      saleManage
        .setFirebaseDB(db)
        .setCollection("items")
        .update(this.item._id, this.item);
    },
    fetchOrder(id, index) {
      // storageManage.setCollection('orders').getWithCondition([{field: 'id', op: '==', value: id}], doc => {
      //   this.order = doc[0];
      //   console.log(doc)
      // })
      this.item = this.items[index];
    },
    fetchImportOrder(id, index) {
      storageManage
        .setCollection("ImportOrder")
        .getWithCondition([{ field: "id", op: "==", value: id }], doc => {
          this.order = doc[0];
          console.log(doc);
        });
    },
    confirmTicket(tic) {
      let total = 0;

      for (let item of tic.items) {
        if (item.type == "combo") {
          for (let i of item.itemsList) {
            storageManage.setCollection("items").get(i._id, doc => {
              storageManage.setCollection("items").update(i._id, {
                stock: +doc.stock - +item.quantity
              });
            });
          }
          let price = +item.price * +item.quantity
          total += price;
        }
        if (item.type == "item") {
          storageManage.setCollection("items").get(item._id, doc => {
            storageManage.setCollection("items").update(item._id, {
              stock: +doc.stock - +item.quantity
            });
          });
        }
        let price =
          (((+item.price - +item.discount_value || 0) *
            (100 - +item.discount_percent || 0)) /
            100) *
          +item.quantity;
        tic.items.total = price;
        total += price;
      }
      storageManage.setCollection("tickets").update(tic._id, {
        is_confirmed: true,
        completed_time: Date.now()
      });
      storageManage
        .setCollection("orders")
        .getWithCondition(
          [{ field: "id", op: "==", value: tic.order_id }],
          doc => {
            storageManage.setCollection("orders").update(doc[0]._id, {
              is_done: true,
              completed_time: Date.now()
            });
          }
        );

      storageManage
        .setCollection("bills")
        .getWithCondition(
          [{ field: "ticket_id", op: "==", value: tic.id }],
          doc => {
            storageManage.setCollection("bills").update(doc[0]._id, {
              is_done: true,
              completed_time: Date.now(),
              total: total.toFixed(6),
              items: tic.items
            });
          }
        );
      
      let d = new Date().getMonth() + '_' + new Date().getFullYear()
      u.setCollection('money').get(d, doc => {
        let time = 0
        if (doc.money < 0 && (+doc.money + +total) > 0) time = Date.now()
        u.setCollection('money').update(d, {
          money: +doc.money + +total,
          time
        })
        if (!doc) {
          u.setCollection('money').set(d, {money: +-item.total})
        }
      })
    }, 
    rejectTicket(tic) {
      let total = 0;

      for (let item of tic.items) {
        if (item.type == "combo") {
          for (let i of item.itemsList) {
            storageManage.setCollection("items").get(i._id, doc => {
              storageManage.setCollection("items").update(i._id, {
                stock: +doc.stock - +item.quantity
              });
            });
          }
          let price = +item.price * +item.quantity
          total += price;
        }
        if (item.type == "item") {
          storageManage.setCollection("items").get(item._id, doc => {
            storageManage.setCollection("items").update(item._id, {
              stock: +doc.stock - +item.quantity
            });
          });
        }
        let price =
          (((+item.price - +item.discount_value || 0) *
            (100 - +item.discount_percent || 0)) /
            100) *
          +item.quantity;
        tic.items.total = price;
        total += price;
      }
      storageManage.setCollection("tickets").update(tic._id, {
        is_confirmed: false,
        completed_time: Date.now()
      });
      storageManage
        .setCollection("orders")
        .getWithCondition(
          [{ field: "id", op: "==", value: tic.order_id }],
          doc => {
            storageManage.setCollection("orders").update(doc[0]._id, {
              is_done: false,
              completed_time: Date.now()
            });
          }
        );
      storageManage
        .setCollection("bills")
        .getWithCondition(
          [{ field: "ticket_id", op: "==", value: tic.id }],
          doc => {
            console.log(doc);
            storageManage.setCollection("bills").update(doc[0]._id, {
              is_done: false,
              completed_time: Date.now()
            });
          }
        );
      for (let item of tic.items) {
        if (item.type == "combo") {
          for (let i of item.itemsList) {
            storageManage.setCollection("items").get(i._id, doc => {
              storageManage.setCollection("items").update(i._id, {
                stock: +doc.stock + +item.quantity
              });
            });
          }
        }
        if (item.type == "item") {
          storageManage.setCollection("items").get(item._id, doc => {
            storageManage.setCollection("items").update(item._id, {
              stock: +doc.stock + +item.quantity
            });
          });
        }
      }
      let d = new Date().getMonth() + '_' + new Date().getFullYear()
      u.setCollection('money').get(d, doc => {
        let time = doc.time
        if ((+doc.money - +total) < 0) time = ''
        u.setCollection('money').update(d, {
          money: +doc.money - +total,
          time
        })
        if (!doc) {
          u.setCollection('money').set(d, {money: +-item.total})
        }
      })
    },
    confirmImportTicket(tic) {
      storageManage.setCollection("ImportTicket").update(tic._id, {
        is_confirmed: true,
        completed_time: Date.now()
      });
      storageManage
        .setCollection("ImportOrder")
        .getWithCondition(
          [{ field: "id", op: "==", value: tic.order_id }],
          doc => {
            storageManage.setCollection("ImportOrder").update(doc[0]._id, {
              is_done: true,
              completed_time: Date.now()
            });
          }
        );
      storageManage
        .setCollection("ImportBill")
        .getWithCondition(
          [{ field: "ticketid", op: "==", value: tic.id }],
          doc => {
            console.log(doc);
            storageManage.setCollection("bills").update(doc[0]._id, {
              is_done: true,
              completed_time: Date.now()
            });
          }
        );
      for (let item of tic.items) {
        if (item.type == "combo") {
          for (let i of item.itemsList) {
            storageManage.setCollection("items").get(i._id, doc => {
              storageManage.setCollection("items").update(i._id, {
                stock: +doc.stock - +item.quantity
              });
            });
          }
        }
        if (item.type == "item") {
          storageManage.setCollection("items").get(item._id, doc => {
            storageManage.setCollection("items").update(item._id, {
              stock: +doc.stock - +item.quantity
            });
          });
        }
      }
    },
    rejectImportTicket(tic) {
      storageManage.setCollection("ImportTicket").update(tic._id, {
        is_confirmed: false,
        completed_time: Date.now()
      });
      storageManage
        .setCollection("ImportOrder")
        .getWithCondition(
          [{ field: "id", op: "==", value: tic.order_id }],
          doc => {
            storageManage.setCollection("ImportOrder").update(doc[0]._id, {
              is_done: false,
              completed_time: Date.now()
            });
          }
        );
      storageManage
        .setCollection("ImportBill")
        .getWithCondition(
          [{ field: "ticketid", op: "==", value: tic.id }],
          doc => {
            console.log(doc);
            storageManage.setCollection("bills").update(doc[0]._id, {
              is_done: false,
              completed_time: Date.now()
            });
          }
        );
      for (let item of tic.items) {
        if (item.type == "combo") {
          for (let i of item.itemsList) {
            storageManage.setCollection("items").get(i._id, doc => {
              storageManage.setCollection("items").update(i._id, {
                stock: +doc.stock - +item.quantity
              });
            });
          }
        }
        if (item.type == "item") {
          storageManage.setCollection("items").get(item._id, doc => {
            storageManage.setCollection("items").update(item._id, {
              stock: +doc.stock - +item.quantity
            });
          });
        }
        let price =
          (((+item.price - +item.discount_value) *
            (100 - +item.discount_percent)) /
            100) *
          +item.quantity;
        tic.items.total = price;
        total += price;
      }
    },
    search() {
      let value = this.searchID.toLowerCase();
      $("#export tbody tr").filter(function() {
        $(this).toggle(
          $(this)
            .text()
            .toLowerCase()
            .indexOf(value) > -1
        );
      });
    },
    search2() {
      let value = this.searchID2.toLowerCase();
      $("#import tbody tr").filter(function() {
        $(this).toggle(
          $(this)
            .text()
            .toLowerCase()
            .indexOf(value) > -1
        );
      });
    }
  },
  created() {
    storageManage.setFirebaseDB(db);
    storageManage
      .setCollection("tickets")
      .getWithConditionRealTime(
        [{ field: "type", op: "==", value: "export" }],
        doc => {
          this.ticket = doc;
          console.log(doc);
        }
      );
    storageManage
      .setCollection("ImportTicket")
      .setOrderBy("created_time", "desc")
      .getAllRealTime(doc => {
        if (doc != "None") {
          this.importTicket = doc;
          console.log(doc);
        }
      });
    storageManage
      .setCollection("items")
      .setOrderBy("created_time", "desc")
      .getAllRealTime(doc => {
        if (doc != "None") {
          this.items = doc;
          console.log(doc);
        }
      });
  }
};
</script>

<style>
